.container {
  max-width: 280px;
  padding: 20px 0;
  @media screen and (min-width: $tablet) {
    max-width: 618px;
    padding: 30px 0 50px;
  }
  @media screen and (min-width: $desktop) {
    max-width: 882px;
    padding-bottom: 46px;
  }
}
.collection {
  list-style: none;
  @media screen and (min-width: $tablet) {
    display: flex;
    flex-wrap: wrap;
    margin-right: -30px;
    margin-bottom: -30px;
  }
  @media screen and (min-width: $desktop) {
  }
}
